* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}

.country-list {
  list-style: none;
  font-weight: 500;
  font-size: 18px;
}

.country {
  display: inline-flex;
  margin: 0;
  padding: 0;
  align-items: center;
  justify-content: space-between;
}

p {
  font-weight: 500;
  font-size: 18px;
  margin: 0;
}

.info-text {
  font-weight: 700;
}